export default {
	data() {
		return {
			colorStyle: '',
			navigation: 0,
			colorNum: 0
		};
	},
	created() {
		this.colorStyle = uni.getStorageSync('viewColor')
		uni.$on('ok', data => {
			this.colorStyle = data
		})
		this.navigation = uni.getStorageSync('navigation')
		uni.$on('navOk', data => {
			this.navigation = data
		})
	},
	methods: {
		colorData(){
			this.colorNum = uni.getStorageSync('statusColor')
			uni.$on('colorOk', data => {
				this.colorNum = data
			})
		}
	}
};
